.App {
  background: linear-gradient(
      217deg,
      rgba(255, 0, 0, 0.8),
      rgba(255, 0, 0, 0) 70.71%
    ),
    linear-gradient(127deg, rgba(153, 0, 255, 0.8), rgba(0, 255, 0, 0) 70.71%),
    linear-gradient(336deg, rgba(0, 0, 255, 0.8), rgba(0, 0, 255, 0) 70.71%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
}

.App-Header {
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  padding: 20px;
  margin-bottom: 20px;
  background-color: rgba(77, 78, 90, 0.212);
  box-shadow: 2px 5px 15px rgba(24, 24, 24, 0.6);
  position: relative;
  width: 100%;
  color: white;
}

.Form {
  background-color: rgba(255, 255, 255, 0.541);
  min-width: 30%;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  box-shadow: 2px 5px 15px rgba(24, 24, 24, 0.6);
}

.results {
  padding: 10px;
  margin: 10px;
}
